@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap);
.spinner{-webkit-animation:rotate 2s linear infinite;animation:rotate 2s linear infinite;z-index:2;width:30px;height:30px}.spinner .path{stroke:#747474;stroke-linecap:round;-webkit-animation:dash 1.5s ease-in-out infinite;animation:dash 1.5s ease-in-out infinite}@-webkit-keyframes rotate{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@-webkit-keyframes dash{0%{stroke-dasharray:1, 150;stroke-dashoffset:0}50%{stroke-dasharray:90, 150;stroke-dashoffset:-35}100%{stroke-dasharray:90, 150;stroke-dashoffset:-124}}@keyframes dash{0%{stroke-dasharray:1, 150;stroke-dashoffset:0}50%{stroke-dasharray:90, 150;stroke-dashoffset:-35}100%{stroke-dasharray:90, 150;stroke-dashoffset:-124}}

.infinity-scroll{max-width:100%;overflow:auto;-ms-overflow-style:none}.infinity-scroll::-webkit-scrollbar{display:none}.infinity-scroll__content{display:inline-flex;align-items:center;width:auto}.infinity-scroll__spinner{margin:0 15px}.infinity-scroll__shadow{height:100%;width:30px;position:absolute;top:0;z-index:2}.infinity-scroll__shadow_right{right:0;background-image:-webkit-gradient(linear, right top, left top, from(rgba(109,117,128,0.15)), to(rgba(109,117,128,0)));background-image:linear-gradient(to left, rgba(109,117,128,0.15), rgba(109,117,128,0))}

.like-btn{width:50px;height:35px;border:2px solid #ff9f9f;box-sizing:border-box;border-radius:4px;display:flex;justify-content:center;align-items:center}

.buy-btn{background:#009900;border-radius:4px;display:flex;justify-content:space-between;align-items:center;padding:0 20px;height:35px;color:white}.buy-btn img{margin-right:10px}


.rating{display:flex;align-items:center;font-family:"Proxima Nova",sans-serif;font-size:15px}.rating__stars-wrapper{margin-right:10px}.rating__count{font-weight:bold}

.product-card{width:204px;color:black;font-family:"Montserrat",sans-serif;font-size:12px;padding:10px 0;background-color:white;display:flex;justify-content:center;flex-direction:column}.product-card>*{padding:0 10px}.product-card:hover{background-color:rgba(255,188,0,0.1)}.product-card:hover .product-card__image-wrapper{background-color:transparent}.product-card_theme_title{padding-top:258px}.product-card_theme_title:hover{background-color:unset}.product-card_theme_title .product-card__section{background-color:white}.product-card__image-wrapper{height:183px;width:180px;border-radius:10px;margin:0 10px 15px 10px;display:flex;justify-content:center;align-items:center;background-color:#f4f2ee}.product-card__image-wrapper img{object-fit:contain}.product-card__price{font-size:18px;font-family:"Proxima Nova",sans-serif}.product-card__name{height:50px;max-width:100%;width:100%;font-weight:600;line-height:15px}.product-card__section{height:31px;width:100%;line-height:31px;font-weight:500;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.product-card__section:nth-child(odd){background-color:#f4f2ee}.product-card__price{font-family:"Proxima Nova",sans-serif;font-size:18px;font-weight:600;display:flex;justify-content:space-between}.product-card__price_red{color:#c00}.product-card__price_old-price{font-size:14px;color:#948f88;text-decoration:line-through}.product-card__btns-wrapper{display:flex;justify-content:space-between;padding-top:15px}

.products-horizontal-viewer{position:relative}.products-horizontal-viewer__title{position:absolute;left:0;background-color:transparent;top:0}@media (max-width: 767px){.products-horizontal-viewer__title{width:100px}}.products-horizontal-viewer__scroll{padding-left:204px}@media (max-width: 767px){.products-horizontal-viewer__scroll{padding-left:100px}}

.page-wrapper{width:100%;max-width:1200px;margin:0 auto;padding:150px 0}

